@use "base/mixins" as mixins;
@use "base/variables" as vars;

.footer {
  color: #f2f2f2;

  &__content {
    display: flex;
    padding: 0 20px 60px 20px;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
