@use "base/mixins" as mixins;
@use "base/variables" as vars;

.about {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: 1;
    display: block;
    top: 690px;
    right: 0;
    width: 100vw;
    height: 2px;
    background-color: #fff;
    transform: skewY(-10deg);
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 1;
    display: block;
    top: 697px;
    left: 30%;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
  }

  &__content {
    margin-top: -170px;
  }

  &__info {
    position: relative;
    width: 700px;
    padding-left: 40px;
    font-weight: 300;
    z-index: 1;
  }

  &__year {
    padding-left: 182px;
    margin-bottom: 180px;
  }

  &__i {
    position: absolute;
    right: 0;
    top: 350px;
  }

  &__mask-wrapper {
    position: absolute;
    top: -200px;
    left: 0;
    width: 100%;
    height: 400px;
    z-index: 0;

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      background: #fff;
      clip-path: polygon(0 0, 100% 72%, 100% 100%, 0% 100%);
    }
  }

  &__mask {
    position: absolute;
    top: 2px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #151515;
    clip-path: polygon(0 0, 100% 72.4%, 100% 100%, 0% 100%);
  }

  &__data {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 110px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #151515;
    margin-top: 140px;
    border-bottom: 1px solid #fff;
    border-top: 1px solid #fff;
    padding: 0 40px;
    text-transform: uppercase;
    font-style: italic;
    font-size: 48px;
  }

  &__data-item {
    display: flex;
    gap: 40px;
  }

  &__letters {
    margin-top: 100px;
    margin-bottom: 250px;
    position: relative;
  }

  &__letters-decor,
  &__details-decor,
  &__langs-decor {
    position: absolute;
    top: 88%;
    width: 100vw;
    height: 400px;
    left: 0;
    object-fit: cover;

    & img {
      position: absolute;
      inset: 0;
      width: 100%;
      object-fit: cover;
    }
  }

  &__letters-item {
    display: flex;
    width: 100%;
    align-items: center;
    gap: 80px;
    justify-content: space-between;
    padding-bottom: 74px;
  }

  &__details {
    position: relative;
    margin-top: -160px;
    margin-bottom: 500px;

    & h2 {
      font-size: 90px;
      font-weight: bolder;
      margin-bottom: 30px;
    }
  }

  &__details-decor {
    top: 120%;
  }

  &__langs {
    position: relative;
    margin-top: -150px;
    margin-bottom: 100px;

    & h2 {
      text-align: right;
      font-size: 90px;
      font-weight: bolder;
      margin-bottom: 60px;
    }
  }

  &__langs-text {
    width: 680px;
    margin-top: 220px;
    position: relative;
    z-index: 1;
    font-weight: 300;
  }

  &__langs-preview {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-left: 12px;

    & img {
      width: 100%;
    }
  }

  &__langs-decor {
    top: 60%;
  }

  &__styles {
    margin-bottom: 100px;
  }

  &__styles-content {
    position: relative;

    & img {
      width: 100%;
    }

    &--footer {
      &::before {
        display: none;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: -40px;
      left: 61%;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: #fff;
    }
  }
}
