@use "base/mixins" as mixins;
@use "base/variables" as vars;

.hero {
  position: relative;
  padding-top: 40px;
  min-height: 750px;
  margin-bottom: 200px;

  &__ball {
    position: absolute;
    bottom: 24px;
    left: 50%;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
  }

  &__preview {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__img {
    position: absolute;
    right: -30px;
    top: 46px;

    & img {
      width: 465px;
      height: 904px;
    }
  }

  &__info {
    position: relative;
    text-align: center;
    margin-left: auto;
    z-index: 1;
    font-weight: 300;
  }
}
