.about {
  position: relative;
}
.about::before {
  content: "";
  position: absolute;
  z-index: 1;
  display: block;
  top: 690px;
  right: 0;
  width: 100vw;
  height: 2px;
  background-color: #fff;
  transform: skewY(-10deg);
}
.about::after {
  content: "";
  position: absolute;
  z-index: 1;
  display: block;
  top: 697px;
  left: 30%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
}
.about__content {
  margin-top: -170px;
}
.about__info {
  position: relative;
  width: 700px;
  padding-left: 40px;
  font-weight: 300;
  z-index: 1;
}
.about__year {
  padding-left: 182px;
  margin-bottom: 180px;
}
.about__i {
  position: absolute;
  right: 0;
  top: 350px;
}
.about__mask-wrapper {
  position: absolute;
  top: -200px;
  left: 0;
  width: 100%;
  height: 400px;
  z-index: 0;
}
.about__mask-wrapper::before {
  content: "";
  position: absolute;
  inset: 0;
  background: #fff;
  clip-path: polygon(0 0, 100% 72%, 100% 100%, 0% 100%);
}
.about__mask {
  position: absolute;
  top: 2px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #151515;
  clip-path: polygon(0 0, 100% 72.4%, 100% 100%, 0% 100%);
}
.about__data {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 110px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #151515;
  margin-top: 140px;
  border-bottom: 1px solid #fff;
  border-top: 1px solid #fff;
  padding: 0 40px;
  text-transform: uppercase;
  font-style: italic;
  font-size: 48px;
}
.about__data-item {
  display: flex;
  gap: 40px;
}
.about__letters {
  margin-top: 100px;
  margin-bottom: 250px;
  position: relative;
}
.about__letters-decor, .about__details-decor, .about__langs-decor {
  position: absolute;
  top: 88%;
  width: 100vw;
  height: 400px;
  left: 0;
  object-fit: cover;
}
.about__letters-decor img, .about__details-decor img, .about__langs-decor img {
  position: absolute;
  inset: 0;
  width: 100%;
  object-fit: cover;
}
.about__letters-item {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 80px;
  justify-content: space-between;
  padding-bottom: 74px;
}
.about__details {
  position: relative;
  margin-top: -160px;
  margin-bottom: 500px;
}
.about__details h2 {
  font-size: 90px;
  font-weight: bolder;
  margin-bottom: 30px;
}
.about__details-decor {
  top: 120%;
}
.about__langs {
  position: relative;
  margin-top: -150px;
  margin-bottom: 100px;
}
.about__langs h2 {
  text-align: right;
  font-size: 90px;
  font-weight: bolder;
  margin-bottom: 60px;
}
.about__langs-text {
  width: 680px;
  margin-top: 220px;
  position: relative;
  z-index: 1;
  font-weight: 300;
}
.about__langs-preview {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-left: 12px;
}
.about__langs-preview img {
  width: 100%;
}
.about__langs-decor {
  top: 60%;
}
.about__styles {
  margin-bottom: 100px;
}
.about__styles-content {
  position: relative;
}
.about__styles-content img {
  width: 100%;
}
.about__styles-content--footer::before {
  display: none;
}
.about__styles-content::before {
  content: "";
  position: absolute;
  top: -40px;
  left: 61%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
}