.hero {
  position: relative;
  padding-top: 40px;
  min-height: 750px;
  margin-bottom: 200px;
}
.hero__ball {
  position: absolute;
  bottom: 24px;
  left: 50%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #fff;
}
.hero__preview {
  position: absolute;
  top: 0;
  left: 0;
}
.hero__img {
  position: absolute;
  right: -30px;
  top: 46px;
}
.hero__img img {
  width: 465px;
  height: 904px;
}
.hero__info {
  position: relative;
  text-align: center;
  margin-left: auto;
  z-index: 1;
  font-weight: 300;
}